/**
 * navbar
 *
 */
#offcanvas {
  height: 100vh;
  position: fixed;
  top: 0;
  visibility: visible;
  color: #6e7680;
  padding-top: 0;
  background: #232e3c;
}
#offcanvas #offcanvas-inner {
  position: absolute;
  top: 0;
  padding-top: 4.5rem;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: auto;
  overflow-x: hidden;
}
#offcanvas #offcanvas-inner > div {
  min-height: 100%;
}
#offcanvas:hover, #offcanvas:hover a {
  color: #c6cad0;
}
#offcanvas a {
  color: #a0a6ae;
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}
#offcanvas a.h5 {
  font-weight: bold;
}
#offcanvas .nav-link {
  padding: 0.35rem 1.5rem 0.35rem 2.5rem;
}
#offcanvas .nav-link.active,
#offcanvas .nav-link:hover,
#offcanvas a:hover {
  color: #e7eaef;
}

#toggle-offcanvas {
  display: none;
}

@media (max-width: 767.98px) {
  #toggle-offcanvas {
    display: block;
    position: fixed;
    top: 0;
    right: 0.5rem;
    z-index: 4;
    font-size: 1.8rem;
    padding: 0.5rem;
    color: #232e3c;
    line-height: 1;
    transition: color 0.3s ease-in-out;
  }
  #offcanvas {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    overflow: auto;
    z-index: 3;
    transition: transform 0.3s ease-in-out, visibility 0.3s ease-in-out;
    transform: translateX(-100%);
  }
  .offcanvas-open {
    overflow: hidden;
  }
  .offcanvas-open #offcanvas {
    transform: translateX(0);
  }
  .offcanvas-open .main-col {
    opacity: 0;
  }
  .offcanvas-open #toggle-offcanvas {
    color: #e7eaef;
  }
}
@media (min-width: 768px) {
  #offcanvas {
    width: 16rem;
  }
  .main-col {
    margin-left: 16rem;
  }
}
